/* Matching.css */
canvas {
  width: 100%;
  height: auto;
  border: 1px solid #ccc;
  background-color: yellow; /* Set background color */
  touch-action: none; /* Prevent default touch behavior */
}

.ResetButton,
.SubmitButton {
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 10px;
  border: 1px solid black;
  cursor: pointer;
  margin: 5px;
}

.ResetButton {
  background-color: white;
  color: black;
}

.SubmitButton {
  background-color: #0060ca;
  color: white;
}

@media (max-width: 600px) {
  .ResetButton,
  .SubmitButton {
    font-size: 14px;
    padding: 8px 16px;
  }

  .ModalContent {
    width: 90%;
  }
}
